/* 自定义一些全局Vux UI */
import Vue from 'vue'
import AlertPlugin from 'vux/src/plugins/alert/index.js'
import LoadingPlugin from 'vux/src/plugins/loading/index.js'
import ToastPlugin from 'vux/src/plugins/toast/index.js'
import ConfirmPlugin from 'vux/src/plugins/confirm/index.js'
import DatetimePlugin from 'vux/src/plugins/datetime/index.js'
import XButton from 'vux/src/components/x-button/index.vue'


Vue.use(AlertPlugin);
Vue.use(LoadingPlugin);
Vue.use(ToastPlugin);
Vue.use(ConfirmPlugin);
Vue.component('x-button', XButton)

Vue.prototype.$g_alert = function (text, cb) {

this.$vux.alert.show({
title: '提示',
content: text,
hideOnBlur: true,
onShow() { },
onHide() { }
})
};

Vue.prototype.$g_toast = function (text) {

this.$vux.toast.text(text, 'middle')
};

Vue.prototype.$g_loadingShow = function (text) {
this.$vux.loading.show({
text: text ? text : '加载中...',
})
};

Vue.prototype.$g_loadingHide = function () {

this.$vux.loading.hide()
};

Vue.prototype.$g_confrimShow = function (text, okCb, cancelCb) {
this.$vux.confirm.show({
'hideOnBlur': true,
'title': '提示',
'content': text,
onCancel() {
cancelCb && cancelCb()
},
onConfirm() {
okCb()
}
})
};

Vue.prototype.$g_confrimShowNoCancel = function (text, okCb) {
this.$vux.confirm.show({
'hideOnBlur': true,
'showCancelButton': false,
'title': '提示',
'content': text,
onConfirm() {
okCb()
}
})
};


Vue.prototype.$g_paramsValidate = function(params) {
let flag = true;
for (var key in params) {
if (params[key] != '0' && !params[key]) {

return false; 
}
}
return flag;
}



