import uinput from './uinput'

export default {
  props: {
    canEditInfo: {
      type: Boolean,
      default: false,
    },
    userMobile: {
      type: String,
    },
    bankAccount: {
      type: String,
    },
    userName: {
      type: String,
    },
    identityCard: {
      type: String,
    },
  },
  data() {
    return {
      isPrpout: true,
    }
  },
  methods: {
    //  点击确定
    onConfirm() {
      this.$emit('confirm', '确定111')
    },
    // 点击取消
    onCancel() {
      this.$emit('cancel', '取消111')
    },
    //确认提交
    confirmPost() {
      this.$emit('click', '')
    },
  },
  components: {
    uinput,
  },
}



