/**
 * 判断传入的字符串是否为一个颜色
 * @param str {String} 颜色
 * @returns {boolean} 结果
 */
export function testColor(str) {
  const reg = new RegExp('^#([0-9a-fA-F]{6}|[0-9a-fA-F]{3})$')
  return reg.test(str)
}

export default {
  name: 'myProcess',
  props: {
    // 背景颜色
    bgColor: {
      type: String,
      validator: (value) => {
        return testColor(value)
      },
      default: '#337ab7',
    },
    // 文本颜色
    txtColor: {
      type: String,
      validator: (value) => {
        return testColor(value)
      },
      default: '#fff',
    },
    showTxt: Boolean, //是否展示文本
    showStriped: Boolean, // 是否需要斑马纹
    showAct: Boolean, // 是否运动
    // 圆角大小
    brdRs: {
      type: Number,
      validator: (value) => {
        return value >= 0 && value <= 20
      },
      default: 4,
    },
    txt: String, // 文本内容
    // 进度条的深度
    processDept: {
      type: Number,
      validator: (value) => value >= 0 && value <= 100,
      default: 30,
    },
    // 进度条的高度
    pcsHeight: {
      type: Number,
      default: 10,
    },
  },
}



